.webix_view {
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: #475466;
  letter-spacing: 0.2px;
  -webkit-font-smoothing: antialiased;
  cursor: default;
  overflow: hidden;
  border: 0px solid #DADEE0;
  background-color: #FFFFFF;
  white-space: normal;
  -webkit-appearance: none;
}
.webix_view.webix_form {
  background-color: #FFFFFF;
}
body {
  -webkit-tap-highlight-color: transparent !important;
}
/*
	webix_layout_{type}
	known types are: line, clean, wide, space;
*/
.webix_abslayout {
  position: relative;
}
.webix_abslayout > .webix_view {
  position: absolute !important;
  z-index: 0;
}
.webix_layout_space {
  background-color: #EBEDF0;
}
.webix_layout_wide {
  background-color: #EBEDF0;
}
.webix_layout_line,
.webix_multiview,
.webix_layout_clean,
.webix_layout_accordion {
  background: transparent;
}
.webix_overlay {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 90;
  text-align: center;
  padding-top: 10px;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
.webix_view > .webix_disabled {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: #ddd;
  opacity: 0.4;
  z-index: 92;
  cursor: not-allowed;
}
.webix_disabled_view {
  overflow: hidden !important;
  position: relative;
}
body.webix_full_screen {
  margin: 0;
  padding: 0;
  overflow: hidden;
}
body.webix_noselect {
  overscroll-behavior: none;
}
.webix_noselect,
.webix_noselect div {
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.webix_selectable {
  -moz-user-select: text;
  -webkit-user-select: text;
  -ms-user-select: text;
  user-select: text;
}
.webix_template {
  height: 100%;
  padding: 4px 12px;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
.webix_clean {
  background: transparent;
}
.webix_clean .webix_template {
  padding: 0px;
}
.webix_section {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  background-color: transparent;
  position: relative;
  color: #444;
  font-size: 10px;
  text-transform: uppercase;
  line-height: 44px;
}
.webix_section:before {
  content: '';
  display: block;
  width: 100%;
  height: 1px;
  background-color: #DADEE0;
  position: absolute;
  top: 50%;
}
.webix_section > .webix_template {
  padding: 0px 8px;
  margin-left: 30px;
  background-color: #FFFFFF;
  display: inline;
  position: relative;
}
.webix_layout_space > .webix_section > .webix_template {
  padding: 0px 8px;
  background-color: #EBEDF0;
}
.webix_layout_wide > .webix_section > .webix_template {
  padding: 0px 8px;
  background-color: #EBEDF0;
}
.webix_layout_line > .webix_section > .webix_template,
.webix_layout_clean > .webix_section > .webix_template {
