/* print css rules*/
.webix_ui_print,
.webix_print_pdf {
  display: none;
}
body.webix_print {
  margin-top: 0px;
}
@media print {
  body.webix_print {
    overflow: visible !important;
    background-color: none !important;
  }
  body.webix_print > * {
    display: none;
  }
  body.webix_print * {
    visibility: hidden;
  }
  .webix_ui_print {
    display: block !important;
    margin: 0 0 0 0!important;
    visibility: visible !important;
  }
  .webix_ui_print * {
    visibility: visible !important;
  }
  .webix_print_noscroll {
    height: auto !important;
    width: auto !important;
  }
